.shop-container {
  padding: 40px 60px;
  font-family: 'Arial', sans-serif;
}

.shop-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  flex-wrap: wrap;
  gap: 15px;
}

.filters {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.filters select {
  padding: 8px 10px;
  font-size: 14px;
  border-radius: 6px;
  border: 1px solid #ccc;
  background-color: #fff;
  cursor: pointer;
}

.view-cart-button {
  background-color: black;
  color: white;
  border: none;
  padding: 10px 16px;
  font-size: 14px;
  border-radius: 8px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 6px;
  transition: background-color 0.2s ease;
}

.view-cart-button:hover {
  background-color: #333;
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  gap: 30px;
}

@media (max-width: 600px) {
  .shop-actions {
    flex-direction: column;
    align-items: flex-start;
  }

  .product-grid {
    grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
  }
}
.brand-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 0.25rem;
}

.brand-subtitle {
  font-size: 1.1rem;
  color: #666;
  margin-bottom: 2rem;
}
