.product-card {
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 16px;
  width: 220px;
  text-align: center;
  transition: box-shadow 0.2s;
  background-color: #fff;
}

.product-card:hover {
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.product-image {
  width: 100%;
  height: 240px;
  object-fit: cover;
  border-radius: 4px;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.product-image.expanded {
  transform: scale(1.8);
  z-index: 10;
  position: relative;
}

.product-card h3 {
  margin: 10px 0 6px;
  font-size: 16px;
  font-weight: 600;
}

.product-card p {
  margin: 4px 0;
  font-size: 14px;
  color: #333;
}

.product-card button {
  margin-top: 10px;
  padding: 8px 16px;
  background-color: black;
  color: white;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.product-card button:hover {
  background-color: #444;
}
