.thank-you-container {
  padding: 30px;
  max-width: 500px;
  margin: 0 auto;
}

.thank-you-container h2 {
  margin-bottom: 10px;
}

.thank-you-container h3 {
  margin-top: 30px;
}

.thank-you-input,
.thank-you-textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
}

.thank-you-error {
  color: red;
  margin-bottom: 10px;
}

.thank-you-button-primary {
  background-color: #000;
  color: #fff;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  margin-right: 10px;
}

.thank-you-button-secondary {
  padding: 10px 20px;
  border: 1px solid #ccc;
  background-color: #f9f9f9;
  cursor: pointer;
}
