.checkout-container {
  padding: 40px 60px;
  font-family: 'Helvetica Neue', sans-serif;
  max-width: 900px;
  margin: 0 auto;
}

.checkout-container h2 {
  font-weight: 500;
  font-size: 1.5rem;
  margin-bottom: 30px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.checkout-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.row {
  display: flex;
  gap: 20px;
}

.row input {
  flex: 1;
  padding: 12px;
  font-size: 1rem;
  border: none;
  border-bottom: 1px solid #ccc;
  outline: none;
}

.row input:focus {
  border-bottom: 1px solid black;
}

.row.justify-end {
  justify-content: flex-end;
}

.checkout-form button {
  padding: 12px 24px;
  background: black;
  color: white;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  transition: background 0.3s;
}

.checkout-form button:hover {
  background: #222;
}.back-home-button {
  background-color: transparent;
  color: black;
  border: 1px solid black;
  padding: 10px 18px;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: 'Helvetica Neue', sans-serif;
  margin-bottom: 30px;
}

.back-home-button:hover {
  background-color: black;
  color: white;
}

