.checkout-progress-wrapper {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.checkout-progress {
  display: flex;
  gap: 10px;
  position: relative;
  width: 100%;
  max-width: 800px;
  justify-content: space-between;
}

.step-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  flex: 1;
}

.step-circle {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #ccc;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
}

.step-circle.active {
  background-color: black;
}

.step-label {
  margin-top: 8px;
  font-size: 12px;
  text-align: center;
}

.step-line {
  position: absolute;
  top: 15px;
  height: 2px;
  background-color: #ccc;
  width: 50%;
  z-index: 0;
}

.step-line.right {
  left: 50%;
}

.step-line.hide {
  display: none;
}

.step-line.active {
  background-color: black;
}
