.cart-sidebar {
  position: fixed;
  top: 0;
  right: 0;
  width: 360px;
  height: 100vh;
  background: white;
  box-shadow: -2px 0 10px rgba(0, 0, 0, 0.15);
  z-index: 1000;
  display: flex;
  flex-direction: column;
  padding: 20px;
  overflow: hidden;
}

.cart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.close-button {
  font-size: 24px;
  background: none;
  border: none;
  cursor: pointer;
}

.empty-cart {
  margin-top: 20px;
  font-size: 16px;
  color: #555;
}

.cart-items {
  flex: 1;
  overflow-y: auto;
  margin-bottom: 15px;
  padding-right: 5px;
}

.cart-items::-webkit-scrollbar {
  display: none;
}

.cart-items {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.cart-item {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
  gap: 10px;
  border-bottom: 1px solid #eee;
  padding-bottom: 10px;
}

.cart-item-image {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.cart-item-info {
  flex: 1;
}

.cart-item-name {
  font-weight: 500;
  font-size: 14px;
  margin-bottom: 4px;
}

.cart-item-price {
  font-size: 13px;
  color: #333;
}

.remove-item {
  background: none;
  border: none;
  font-size: 18px;
  cursor: pointer;
  color: #c00;
}

.cart-footer {
  border-top: 1px solid #ddd;
  padding-top: 15px;
}

.cart-subtotal {
  font-weight: bold;
  font-size: 16px;
  margin-bottom: 10px;
}

.checkout-button {
  width: 100%;
  background-color: black;
  color: white;
  border: none;
  padding: 10px 0;
  font-size: 14px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.checkout-button:hover {
  background-color: #333;
}

.checkout-button:disabled {
  background-color: #999;
  cursor: not-allowed;
}
.cart-sidebar {
  display: flex;
  flex-direction: column;
}

.cart-items {
  flex: 1;
  overflow-y: auto;
  padding-right: 10px;
  margin-bottom: 20px;
}

.cart-footer {
  position: sticky;
  bottom: 0;
  background-color: white;
  padding: 15px 0;
  border-top: 1px solid #ddd;
}

.cart-quantity-controls {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 5px;
}

.cart-quantity-controls button {
  width: 24px;
  height: 24px;
  border: 1px solid #ccc;
  background: none;
  cursor: pointer;
}
